import React,{Component} from 'react';
import { Button, Modal, ModalHeader, ModalBody, Row, Label, Col } from 'reactstrap';
import { LocalForm, Control, Errors } from 'react-redux-form';

const required = (val) => {
   return( val && val.length );
}
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);

class CommentForm extends Component{

    constructor(props){
        super(props);
        this.state = {
            isModalOpen: false
        }
        this.toggleModal = this.toggleModal.bind(this);
        this.handleCommentSubmit =  this.handleCommentSubmit.bind(this);
    }

    toggleModal() {
        this.setState({
          isModalOpen: !this.state.isModalOpen
        });
    }

    handleCommentSubmit(values) {
        this.toggleModal();
        
        this.props.addComment(this.props.dishId,values.rating,values.yourname,values.comment);
    }

    render(){
        return(
            <div className="container">
                <Button outline onClick={this.toggleModal} ><span className="fa fa-pencil-square-o"></span>Submit Comment</Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={(values) => this.handleCommentSubmit(values)}>
                            <Row className="form-group">
                                <Label htmlFor="rating" md={2}>Rating</Label>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size: 10, offset: 0}}>
                                    <Control.select model=".rating" name="rating"
                                        className="form-control">
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                        <option>5</option>
                                    </Control.select>
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="yourname" md={10}>Your Name</Label>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size: 10, offset: 0}}>
                                    <Control.text model=".yourname" name="yourname" id="yourname"
                                        className="form-control"
                                        placeholder="Your Name"
                                        validators={{
                                                required, minLength: minLength(3), maxLength: maxLength(15)
                                        }}>
                                    </Control.text>
                                    <Errors className="text-danger"
                                                model=".yourname"
                                                show="touched"
                                                messages={{
                                                    required: 'Required',
                                                    minLength: 'Must be greater than 2 numbers',
                                                    maxLength: 'Must be 15 numbers or less',
                                                }}
                                        >
                                        </Errors>

                                </Col>
                                                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="comment" md={2}>Comment</Label>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size: 10, offset: 0}}>
                                    <Control.textarea model=".comment" name="comment" id="comment"
                                        className="form-control"
                                        rows="12"    
                                   />    
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size:10, offset: 0}}>
                                    <Button type="submit" color="primary">
                                    Submit
                                    </Button>
                                </Col>
                            </Row>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </div>
        )
    }
}
export default CommentForm;